.container {
    font-family: 'Inter', sans-serif;
    padding: 20px;
    max-width: 800px;
    margin: auto;
    text-align: center;
}

.domain-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

.domain-item {
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 10px;
    margin: 10px;
    width: 180px;
}

.domain-item.sold {
    background-color: #f8d7da;
    color: #721c24;
}

.domain-item.available {
    background-color: #d4edda;
    color: #155724;
}

button {
    margin-top: 10px;
    padding: 5px 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

button:disabled {
    background-color: #ccc;
    cursor: not-allowed;
}
